import { getTranslations } from '@/lib/i18n'

export async function generateMetadata({
  params,
}: {
  params: Promise<{ lang: string }>
}) {
  const { lang } = await params // 等待解析 params
  const t = await getTranslations(lang)
  
  return {
    title: t.pc_top_title,
    description: t.pc_top_description,
    alternates: {
      canonical: `/${lang}/mobile/top`,
      languages: {
        'en': '/en/mobile/top',
        'zh': '/zh/mobile/top',
        'ja': '/ja/mobile/top',
      },
    },
  }
}

